.welcome-content{    
    background-size: cover;
    background-position: center;
    height: 100%;
    padding: 4.6875rem 3.125rem;
    position: relative;
    z-index: 1;
    border-top-left-radius: 0.3125rem;
    border-bottom-left-radius: 0.3125rem;
    &::after{
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: var(--primary);
        opacity: 0.75;
        z-index: -1;
        border-top-left-radius: 0.3125rem;
        border-bottom-left-radius: 0.3125rem;
    }
    .welcome-title{
        color: $white;
        font-weight: 500;
        font-size: 1.25rem;
    }
    p{
        color: $white;
    }

    .brand-logo{
        a{
            display: inline-block;
            margin-bottom: 1.25rem;
            font-weight: 700;
            color: $white;
            font-size: 1.25rem;
        img{
            width: 6.25rem;
        }
        }
    }

    .intro-social{
        position: absolute;
        bottom: 4.6875rem;
        ul{
            margin-bottom: 0;
            li{
                display: inline-block;
                a{
                    color: $white;
                    font-size: 0.875rem;
                    padding: 0rem 0.4375rem;
                }
            }
        }
    }
}

.auth-form{
    padding: 3.125rem 3.125rem;
	@include respond('phone') {
		padding: 1.875rem;
	}	
    .btn{
        height: 3.125rem;
        font-weight: 700;
    }
    .page-back{
        display: inline-block;
        margin-bottom: 0.9375rem;
    }
}
.login-form{
	margin: 0 58px;
    background: white;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
	@include respond ('phone'){
		margin:0 10px;
	}
}
.forget-main{
	.forget-tab{
		border-radius:$radius;
		&:active{
			color:var(--primary)!important;
		}
	}
}
.bg-login{
	background:rgba(255,255,255,0.3);
}

.authincation-content.style-1{
	border-radius: 30px;
	height: 590px;
	width:100%;
	.main-title{
		font-size: 60px;
		text-transform: capitalize;
		margin-bottom: 50px;
		font-family: cursive;
	}
	.img-bx{
		padding: 20px;
		height: 100%;
		img{
			object-fit:cover;
			border-radius:30px;
			height: 100%;
		}
	}
	.auth-form{
		padding: 60px 50px 60px 0px;
	}
	@include respond ('phone-land') {
		height:auto;
		margin: 50px 0px;
		.main-title{
			font-size: 40px;
		}
		.img-bx{
			height:auto;
			img{
				height:auto;
			}
		}
		.auth-form{
			padding: 20px 25px;
		}
	}
}

.form-control.solid{
	background-color:#f1f1f1;
	color:#000;
	font-weight: 500;
    font-size: 16px;
}